import React, { Component } from 'react';
import { TimeSelect, TimeRangePicker } from 'element-react';
import './index.scss';
import Codemirror from 'react-codemirror';
import { connect } from 'react-redux';
import { fromJS, is } from 'immutable';

class XtimePicker extends Component {
	state = {
		value: new Date(2016, 9, 10, 8, 45),
		value2: [new Date(2016, 9, 10, 8, 40), new Date(2016, 9, 10, 9, 40)]
	}

	componentDidMount() {
	}

	shouldComponentUpdate(nextProps, nextState) {
		return !is(fromJS(this.props), fromJS(nextProps)) || !is(fromJS(this.state), fromJS(nextState))
	}
	
	handleUpdate = (n) => {
		console.log(n)
		this.setState({
			value: n
		})
	}

	handleRangeUpdate = (val) => {
		console.log(val)
		this.setState({
			value2: val
		})
	}

	render() {
		const { defaultOptions} = this.props.configs

		return (
			<div id="x-time-picker">
				<h3>Time Picker 时间选择器</h3>
				<div className="mt-20 mb-20">
					<TimeSelect
						start="08:30"
						step="00:15"
						end="18:30"
						maxTime="12:30"
						onChange={this.handleUpdate}
						value={this.state.value}
						placeholder="选择时间"
						className="mr-20"
					/>
					<TimeRangePicker
						pickerWidth={300}
						onChange={this.handleRangeUpdate}
						placeholder="选择时间"
						value={this.state.value2}
					/>
				</div>
				<Codemirror value={
					`<TimeSelect
	start="08:30"
	step="00:15"
	end="18:30"
	maxTime="12:30"
	onChange={this.handleUpdate}
	value={this.state.value}
	placeholder="选择时间"
	className="mr-20"
/>
<TimeRangePicker
	pickerWidth={300}
	onChange={this.handleRangeUpdate}
	placeholder="选择时间"
	value={this.state.value2}
/>`
				} options={defaultOptions} />

			</div>
		);
	}
}

export default connect(state => ({
	configs: state.configs
}), {

})(XtimePicker)
